svg .sherlock_costume {
  opacity: 0;
}

svg .sherlock_costume.loading {
  -webkit-animation: sherlock 3s infinite;
          animation: sherlock 3s infinite;
}

svg .sherlock_costume.search-complete {
  opacity: 1;
}

@-webkit-keyframes sherlock {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}

@keyframes sherlock {
  0% {
    opacity: 0;
  }
  40% {
    opacity: 1;
  }
  60% {
    opacity: 1;
  }
  100% {
    opacity: 0;
  }
}


