/**
 * Color Palet:
 *  #e23636	(226,54,54)
 *  #000000	(0,0,0)
 *  #504a4a	(80,74,74)
 *  #518cca	(81,140,202)
 *  #f78f3f	(247,143,63)
 */

@import url('https://fonts.googleapis.com/css?family=PT+Sans');

html {
  height: 100%;
}

body {
  height: 100%;
  display: block;
  margin: 0px;
  background: -webkit-gradient(linear, left top, left bottom, from(Indigo), to(MidnightBlue));
  background: -webkit-linear-gradient(Indigo, MidnightBlue);
  background: -o-linear-gradient(Indigo, MidnightBlue);
  background: linear-gradient(Indigo, MidnightBlue);
  background-repeat: no-repeat;
  background-attachment: fixed;
}

a {
  text-decoration: none;
  cursor: pointer;
  color: black;
  margin: 20px;
}

a > div {
  background: -webkit-gradient(linear, left top, left bottom, from(rgba(255, 255, 255, 0.5)), to(rgba(128, 128, 128, 0.5)));
  background: -webkit-linear-gradient(rgba(255, 255, 255, 0.5), rgba(128, 128, 128, 0.5));
  background: -o-linear-gradient(rgba(255, 255, 255, 0.5), rgba(128, 128, 128, 0.5));
  background: linear-gradient(rgba(255, 255, 255, 0.5), rgba(128, 128, 128, 0.5));
}

a:hover > div {
  background: -webkit-gradient(linear, left top, left bottom, from(gray), to(white));
  background: -webkit-linear-gradient(gray, white);
  background: -o-linear-gradient(gray, white);
  background: linear-gradient(gray, white);
}

.app {
  position: relative;
  width: 100%;
  height: 100vh;
  color: #333;
  -ms-flex-align: center;
      align-items: center;
}

.app > h1 {
  color: white;
}

.landing-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  -ms-flex-align: center;
      align-items: center;
  -ms-flex-pack: start;
      justify-content: flex-start;
  height: 100vh;
}

.landing {
  background-color: #504a4a;
  padding: 15px 30px;
  border-radius: 5px;
}

.landing > h1 {
  color: #e23636;
  font-family: 'PT Sans', sans-serif;;
  font-weight: normal;
}

.landing input {
  width: 100%;
  padding: 10px;
  border-radius: 4px;
}

.above-screen {
  margin-top: -50%;
}

.main-container {
  height: 140px;
  margin-top: 8%;
  width: 800px;
  text-align: center;
  -webkit-transition: margin-top .7s ease-in-out;
  -o-transition: margin-top .7s ease-in-out;
  transition: margin-top .7s ease-in-out;
}

.main-container img {
  margin: 20px auto;
}

.main-container h3 {
  font-size: 2.5em;
  color: #fff;
  font-weight: 900;
  -webkit-transition: opactiy 0.6s ease-in;
  -o-transition: opactiy 0.6s ease-in;
  transition: opactiy 0.6s ease-in;
}

.main-container .search-container {
  position: relative;
  width: 100%;
  -webkit-transition: width .6s ease-in-out;
  -o-transition: width .6s ease-in-out;
  transition: width .6s ease-in-out;
}

.search-bar {
  width: 100%
}

input.search-bar {
  background: #fff;
  font-size: 1.5em;
  padding: 8px;
}

.search-button {
  position: absolute;
  top: 0;
  right: 0;
  padding: 7px 10px 10px;
  background: #181818;
  border: none;
  cursor: pointer;
}

.search-button:hover {
  background: #8CD2EC;
}

.search-icon {
  display: inline-block;
  background: transparent;
  border-radius: 50%;
  border: 5px solid #fff;
  height: 30px;
  width: 30px;
  position: relative
}

.search-icon:after {
  content: "";
  position: absolute;
  top: 21.604999999999997px;
  left: 14.302499999999998px;
  -webkit-transform: rotate(45deg);
  -ms-transform: rotate(45deg);
      transform: rotate(45deg);
  height: 5px;
  width: 15px;
  background-color: #fff;
  border-radius: 10px
}

.fly-up {
  margin-top: -80px;
}

.fly-up .search-container {
  width: 65%;
}

/** FLEX BOX **/
*,:after,:before {
    -webkit-box-sizing: border-box;
            box-sizing: border-box
}






